<template>
  <div v-loading='loading'>
    <cms-breadcrumb to="/physicalCompany" @click="rest">
      <li>{{$route.name}}</li>
    </cms-breadcrumb>
    <div class="cms-form-layout">
      <cms-prompt>
        <li>带 * 为必填项</li>
      </cms-prompt>
      <el-form ref="form" :rules="saveRules" :model="formData" label-width="210px">
        <el-form-item label="物流公司名称" prop="cnName" :error='errorMessage.cnName'>
          <el-input v-model="formData.cnName" :autofocus="true" ref="first" maxlength="30"></el-input>
        </el-form-item>
         <el-form-item label="物流公司编码" prop="code" :error='errorMessage.code'>
          <el-input v-model="formData.code" maxlength="20"></el-input>
        </el-form-item>
        <el-form-item label="英文名称" prop="enName"  :error='errorMessage.enName' >
          <el-input v-model="formData.enName" maxlength="30"></el-input>
        </el-form-item>
        <el-form-item label="名称首字母" prop="initialName"  :error='errorMessage.initialName' >
          <el-input v-model="formData.initialName" maxlength="10"></el-input>
        </el-form-item>
         <el-form-item label="LOGO" prop="logoImgId" ref="logoImgId" :error='errorMessage.logoImgId' class="brands-logo">
            <cms-picture-zone v-model="formData.logoImgId" v-if="status" >
              <cms-button type='info' size='small'>上传</cms-button>
            </cms-picture-zone>
          </el-form-item>
           <el-form-item label="是否启用" prop="isEnable" :error='errorMessage.isEnable'>
          <cms-switch v-model="formData.isEnable"></cms-switch>
          <div class='gray'>设置物流公司是否能被商家使用</div>
        </el-form-item>
        <el-form-item label="排序" prop="sortNum"  :error='errorMessage.sortNum' >
          <el-input v-model="formData.sortNum" maxlength="2"></el-input>
        </el-form-item>
        <el-form-item label="网址" prop="url"  :error='errorMessage.url' >
          <el-input v-model="formData.url" maxlength="30"></el-input>
        </el-form-item>
        <el-form-item label="简介" prop="summary"  :error='errorMessage.summary' >
          <el-input v-model="formData.summary" type="textarea" :rows="6" maxlength="200"></el-input>
        </el-form-item>
      </el-form>
      <div class="cms-form-footer">
        <cms-button  @click="save(false)" v-color >保存并新增</cms-button>
        <cms-button @click="save(true)"  v-color >保存</cms-button>
        <cms-button type='rest' @click="rest">重置</cms-button>
      </div>
    </div>
  </div>
</template>
<script>
import pinyin from 'js-pinyin'
import rules from '@/rules'
import form from '@/mixins/form.js'
import {
  logisticsCompanySave
} from '@/api/setting/logisticsCompany.js'

var defaultData = {
  'cnName': '',
  'enName': '',
  'url': '',
  'summary': '',
  'sortNum': 10,
  'initialName': '',
  'logoImgId': '',
  'code': '',
  'isEnable': true
}
export default {
  name: 'physicalCompanySave',
  mixins: [form],
  data() {
    var validateCode = (rule, value, callback) => { // 验证物流公司编码
      const reg = /^[0-9a-zA-Z]+$/
      if (value === '') {
        callback(new Error('物流公司编码不能为空'))
      } else if (!reg.test(value)) {
        callback(new Error('只能输入数字或者英文字母'))
      } else {
        callback()
      }
    }
    var validateEng = (rule, value, callback) => { // 验证物流公司编码
      const reg = /^[a-zA-Z]+$/
      if (value === '') {
        callback()
      } else if (!reg.test(value)) {
        callback(new Error('只能输入英文字母'))
      } else {
        callback()
      }
    }
    var validateInitial = (rule, value, callback) => { // 验证名称首字母
      const reg = /^[A-Z]+$/
      if (value === '') {
        callback(new Error('名称首字母不能为空'))
      } else if (!reg.test(value)) {
        callback(new Error('只能输入大写英文字母'))
      } else {
        callback()
      }
    }
    return {
      formData: Object.assign({}, defaultData),
      saveRules: {// 新增规则
        cnName: [rules.required('物流公司名称不能为空'), rules.len(0, 30)],
        enName: [rules.len(1, 30), rules.custom(validateEng)],
        code: [rules.required(), rules.custom(validateCode)],
        initialName: [rules.required(), rules.custom(validateInitial)],
        logoImgId: [rules.required('LOGO不能为空')],
        isEnable: [rules.required(null, 'blur', 'boolean')],
        sortNum: [rules.required('排序值不能为空'), rules.number()],
        url: [rules.url()]
      }
    }
  },
  watch: {
    'formData.cnName': {// 自动填充英文首字母
      handler(newV, oldV) {
        pinyin.setOptions({ checkPolyphone: false, charCase: 0 })
        if (newV === '') {
          return false
        } else {
          const str = pinyin.getFullChars(newV)// 获取拼音组成的字符串
          this.formData.initialName = str.charAt(0).toUpperCase()
        }
      },
      deep: true
    }
  },
  methods: {
    save(state) { // 添加
      this.saveForm(logisticsCompanySave, this.formData, state, '/physicalCompany', 'list')
    }
  },
  activated() {
    this.autoFocus()
    if (this.$route.query.type === 'save') {
      this.formData = Object.assign({}, defaultData)
    }
  }
}
</script>




